// imagepicker.contentMode: "Choose between \"Image\" and \"Video\" to set the content mode of the media selector. If \"Image\" is selected, ensure that all options provided are image files in the following formats: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Similarly, if \"Video\" is selected, ensure that all options are direct links to video files in the following formats: MP4, MOV, WMV, FLV, AVI, MKV. Please note that YouTube links are not supported for video options." => "Medya seicinin ierik modunu ayarlamak iin \"Grnt\" ve \"Video\" arasnda seim yapn. \"Resim\" seilirse, salanan tm seeneklerin u biimlerdeki resim dosyalar olduundan emin olun: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Benzer ekilde, \"Video\" seilirse, tm seeneklerin aadaki formatlardaki video dosyalarna dorudan balantlar olduundan emin olun: MP4, MOV, WMV, FLV, AVI, MKV. Video seenekleri iin YouTube balantlarnn desteklenmediini ltfen unutmayn."
